<template>
  <div class="container bg-blue center">
    <h1 v-for="text in textDisplay" v-bind:key="text" class="slide-up">
      {{ text }}
    </h1>
    <img v-if="counter == 3" src="@/assets/rickroll.jpg" alt="" />
    <audio src="@/assets/rickroll.mp3" ref="rickroll"></audio>

    <button @click="showNext" v-if="counter < 3" counter>Surprise me!</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      counter: 0,
      textDisplay: [],
      texts: ["Roses are red.", "Violets are blue."]
    };
  },
  methods: {
    showNext() {
      if (this.counter < 2) {
        this.textDisplay.push(this.texts[this.counter]);
      }
      this.counter++;
      if (this.counter == 3) {
        this.$refs.rickroll.play();
      }
    }
  }
};
</script>

